import env from "../../../constants/Environment";
import { motion } from "framer-motion";

interface Props {
  calledFromBuilder?: boolean;
}

const Zapier = ({ calledFromBuilder = true }: Props) => {
  return (
    <motion.div
      initial={{ opacity: 0, translateY: 15 }}
      animate={{ opacity: 1, translateY: 0 }}
      exit={{ opacity: 0 }}
      transition={{ duration: 0.4 }}
      className="general-container settings-container zapier_container"
    >
      {!calledFromBuilder && (
        <div className="api-header fx al-cnt mb-30">
          <h1>Zapier</h1>
        </div>
      )}
      <div className="settings_wrapper">
        <div className="bg-transparent">
          <h1 className="settings_heading">Zapier</h1>
          <div className="zapier-container">
            <zapier-full-experience
              client-id={
                env()?.isAI
                  ? "eLWrxNEWYuabV7sXpe25HtMqNvvsp8NMmrU6SrkA"
                  : "usWqeDInYGiW56OzFtvLVzDZWM5Ii9W5rNAqKzPX"
              }
              theme="light"
              app-search-bar-display="show"
            />
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default Zapier;
